* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

header {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  padding-top: 1em;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  font-family: lato;
  background: #222;
}

.bottomLine {
  height: 2px;
  background: #fff;
  margin-top: 1em;
  width: 0%;
}

.bottomLineWide {
  -webkit-transition: width 1.5s;
  -o-transition: width 1.5s;
  transition: width 1.5s;
  width: 100%;
}



